@mixin _private-apply-font($type-scale) {
    font-size: var(--mat-sys-#{$type-scale}-size);
    font-weight: var(--mat-sys-#{$type-scale}-weight);
    letter-spacing: var(--mat-sys-#{$type-scale}-tracking);
    line-height: var(--mat-sys-#{$type-scale}-line-height);
}

:host {
    display: grid;
    grid-auto-rows: min-content;
    grid-row-gap: 24px;
    overflow: hidden;

    width: 380px;

    border-radius: var(--mat-sys-corner-large);
    background-color: var(--mat-sys-surface-container);

    .heading {
        display: grid;
        grid-template-columns: 1fr;
        grid-auto-flow: column;
        grid-column-gap: 4px;

        padding: 12px 24px;

        background-color: var(--mat-sys-surface-container-high);
    }

    .content {
        position: relative;
        padding-inline: 24px;
    }

    .progress {
        position: absolute;
        inset-inline: 0;
        top: 0;
    }
}

.content {
    display: grid;
    grid-gap: 16px;
}

.title {
    @include _private-apply-font(headline-small);

    margin: 0;
}

.form {
    display: grid;
    grid-row-gap: 16px;
}

.actions {
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: max-content;
    grid-column-gap: 8px;
    justify-content: end;
    padding-inline: 24px;
    padding-bottom: 16px;
}
